import AppSearchAPIConnector from "@elastic/search-ui-app-search-connector";
import React from "react";
import {
  ErrorBoundary,
  Facet,
  SearchProvider,
  SearchBox,
  Results,
  PagingInfo,
  ResultsPerPage,
  Paging,
  Sorting,
  WithSearch
} from "@elastic/react-search-ui";
import {
  BooleanFacet,
  Layout,
  SingleLinksFacet,
  SingleSelectFacet
} from "@elastic/react-search-ui-views";
import "@elastic/react-search-ui-views/lib/styles/styles.css";
const connector = new AppSearchAPIConnector({
  searchKey: "search-mrm18uc57he4ssbzr2c3bf9i",
  engineName: "test-mbi",
  endpointBase: "https://my-deployment-bce907.ent.eu-west-3.aws.elastic-cloud.com"
});
const config = {
  debug: true,
  alwaysSearchOnInitialLoad: true,
  apiConnector: connector,
  hasA11yNotifications: true,
  searchQuery: {
    // section result field
    result_fields: {
        document: {
          snippet: {
            fallback: true
          }
        },
        nps_link: { 
          raw: {}
        },
        content: {
          snippet: {
            size: 1000,
            fallback: true
          }
        },
        date_established: { 
          raw: {} 
        },
    },
    // Search field
    search_fields: {
      document: {
        weight: 5
      },
        content: {
          weight: 10
        },
    },
    // parameter facets
    disjunctiveFacets: [""],
    facets: {
            date_established: { type: "value", size: 30 },
        }
      }
};
export default function App() {
  return (
    <SearchProvider config={config}>
      <WithSearch
        mapContextToProps={({ wasSearched }) => ({
          wasSearched
        })}
      >
        {({ wasSearched }) => {
          return (
            <div className="App">
              <ErrorBoundary>
                <Layout
                  header={
                  <SearchBox 
                  autocompleteResults={{
                    sectionTitle: "Suggested Results",
                    titleField: "title",
                    urlField: "nps_link"
                  }}
                  autocompleteSuggestions={{
                    sectionTitle: "Suggested Queries"
                  }}                
                 /* debounceLength={0} *//>}
                  sideContent={<div>
    <Facet
      field="date_established"
      label="Date Established"
      filterType="any"
    />
                  </div>}
                  bodyContent={
                    <Results
                      titleField="document"
                      urlField="nps_link"
                      thumbnailField="image_url"
                      shouldTrackClickThrough
                    />
                  }
                  bodyHeader={
                    <React.Fragment>
                      {wasSearched && <PagingInfo />}
                      {wasSearched && <ResultsPerPage />}
                    </React.Fragment>
                  }
                  bodyFooter={<Paging />}
                />
              </ErrorBoundary>
            </div>
          );
        }}
      </WithSearch>
    </SearchProvider>
  );
}
